import React, { useState, useEffect } from 'react';
import axios from "axios";

export default function About(){

    const [data, dataUpdate] = useState({data:''});
    const [eblan, uEblan] = useState();


    useEffect(() => {

        async function fetchData() {
            await fetch('http://localhost:8080/about')
                .catch('Error Caoused :D')
                .then(response => response.text())
                .then(data => dataUpdate({data: data}))
        }
        fetchData().catch(error => {console.log(error); dataUpdate({data: 'Server Unavaible'})});
    }, []);

    return(
        <div>
            <p>This is a ABOUT page :)</p>
            <p>{data.data}</p>
            <p>fetched answer: {}</p>
        </div>
    )
}